@tailwind base;
@tailwind components;
@tailwind utilities;

/* ‚ú® Í∏∞Î≥∏ Í∏ÄÍº¥ Î†åÎçîÎßÅ */
html,
body {
  margin: 0;
  padding: 0;
  background-color: #0e0717;
  color: #f5f3ff;
  font-family: "Pretendard", "Inter", "sans-serif";
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* üíú Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùº */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: rgba(25, 15, 40, 0.8);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #8b5cf6, #6d28d9);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #a78bfa, #7c3aed);
}

/* üí´ ÌÖçÏä§Ìä∏ ÏÑ†ÌÉù Í∞ïÏ°∞ÏÉâ */
::selection {
  background: rgba(139, 92, 246, 0.35);
  color: #fff;
}

/* üåô Ïä§ÌÅ¨Î°§ Î¶¨Îπå Ïï†ÎãàÎ©îÏù¥ÏÖò */
.js-reveal {
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 0.9s ease, transform 0.9s ease;
}
.js-reveal.visible {
  opacity: 1;
  transform: translateY(0);
}

/* ‚öôÔ∏è Î†àÏù¥ÏïÑÏõÉ ÏïàÏ†ïÏÑ± */
main,
section {
  position: relative;
  z-index: 10;
}

/* üí° Î≤ÑÌäº hover Í∏∞Î≥∏ ÏÑ§Ï†ï */
button {
  transition: all 0.25s ease;
}
button:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.5);
}

/* üíé Î∞òÏùëÌòï Ïó¨Î∞± Î≥¥Ï†ï */
@media (max-width: 768px) {
  section {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}

/* üíª ÏΩîÎìú Î∏îÎ°ù Ïä§ÌÉÄÏùº (API Îì± ÌéòÏù¥ÏßÄÏö©) */
pre {
  background: rgba(25, 15, 40, 0.8);
  border-radius: 0.75rem;
  padding: 1rem;
  color: #e9d5ff;
  font-family: "JetBrains Mono", monospace;
  font-size: 0.9rem;
  overflow-x: auto;
  border: 1px solid rgba(109, 40, 217, 0.4);
}
